:root {
  --primary-main: #52ab98;
  --primary-light: #f3f9f8;
  --primary-dark: #00a989;

  --secondary-main: #6076b8;
  --secondary-light: #e3ebfb;
  --secondary-dark: #304184;

  --text-primary: "#212B36";
  --text-secondary: "#637381";

  --grey-input-border: "#BDBDBD";
  --grey-facebook: "#E4E6EB";
  --grey-input-bg: "#FAFAFA";
  --grey-light: "#F9FBFC";

  --light-color: #f2f4fe;
  --white-color: #ffffff;
}
a {
  color: #637381;
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  color: var(--primary-main);
}

/* Start Footer */
.main_footer {
  background-color: #f2f2f2;
  padding: 10px 0px;
}
/* End Footer */
.radio_option {
  width: 47%;
}
.social_icon_btn {
  font-size: 32px;
}
/* Question Stepper */
.stepper :global .MuiStepLabel-label.Mui-active {
  color: var(--primary-main);
}
.stepper :global .MuiStepConnector-line,
.stepper :global .MuiStepContent-root {
  border-color: #ede7f6;
}
/* Question Stepper */

.Nutritionist_card {
  cursor: pointer;
  margin-bottom: 0.55rem !important;
  transition: 0.4s all;
}

.Nutritionist_card:last-child {
  margin-bottom: 0;
}
.Nutritionist_card.active {
  box-shadow: rgb(149 157 165 / 10%) 0px 8px 24px;
  border: 1px solid var(--primary-main) !important;
  border-right: 4px solid var(--primary-main) !important;
}

.Nutritionist_card:hover {
  border-color: var(--primary-main) !important;
}
.consult_type_icon {
  border-radius: 100px;
  font-size: 30px !important;
  padding: 5px;
  box-shadow: rgb(214 214 214 / 11%) 0px 8px 24px;
  margin: 0 3px;
}

.plan_selection_card_detail :global .MuiTypography-root {
  display: flex;
  align-items: flex-start;
  margin-bottom: 7px;
}
.plan_selection_card_detail :global .MuiSvgIcon-root {
  font-size: 18px;
  margin-right: 5px;
}
.plan_card_align {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.selection_info_wrap {
  border: 1px solid #ddd;
  box-shadow: rgba(149, 157, 165, 7%) 0px 8px 24px;
  border-radius: 10px;
  padding: 12px 20px;
  height: 100%;
  margin-top: 1rem;
  text-align: center;
}

.profile_details_chips {
  font-size: 13px !important;
  font-weight: 500;
  border-radius: 10px !important;
}

/* recipe page */
.recipe_banner_img {
  height: 40vh;
  background-size: cover !important;
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .radio_option {
    width: 100%;
  }
  .main_footer :global .MuiDivider-root {
    display: none;
  }
}

/* For Ipad and Ipad-Pro devices 1024x1366 Media Query:- */
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .recipe_banner_img {
    height: 20vh;
  }
}

/* For Mobile devices Media Query:- */
@media only screen and (max-width: 767px) {
  .articleSwiper .swiper-slide img {
    height: 250px !important;
  }
  .recipe_banner_img {
    height: 30vh;
  }
}
