@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --primary-main: #52ab98;
  --primary-light: #e0faeb;
  --primary-dark: #297b79;

  --secondary-main: #6076b8;
  --secondary-light: #e8ebf5;
  --secondary-dark: #6478b5;

  --text-primary: "#56636F";
  --text-secondary: "#637381";
  --text-disabled: "#919EAB";

  --grey-input-border: "#BDBDBD";
  --grey-facebook: "#E4E6EB";
  --grey-input-bg: "#FAFAFA";
  --grey-light: "#F9FBFC";

  --blue-light: #f4f9fc;

  --light-color: #f2f4fe;
  --white-color: #ffffff;
  --black-color: #0c0c0c;
}

/* Scroll Bar Style Begin */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: #f9fafb;
}

::-webkit-scrollbar-track {
  background-color: #f9fafb;
  border-radius: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(99, 115, 129, 0.48);
  border-radius: 10px;
}

.scrollbar-none {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scrollbar-none::-webkit-scrollbar {
  display: none;
}

/* Scroll Bar Style End */

body {
  width: 100%;
  height: auto;
  font-family: "Noto Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
}

.text-white {
  color: #ffffff !important;
}
.bg-white {
  background-color: var(--primary-main);
}

.bg-primary {
  background-color: var(--primary-main);
}

.bg-primary-light {
  background-color: var(--primary-lighter);
}

.text-primary {
  color: var(--primary-main);
}

.text-primary-light {
  color: var(--primary-lighter);
}

.text-primary-light:hover {
  color: var(--black-color);
}

.bg-secondary {
  background-color: var(--secondary-main);
}

.bg-secondary-light {
  background-color: var(--secondary-lighter);
}

.text-secondary-light {
  color: var(--secondary-lighter);
}

.text-secondary-light:hover {
  color: var(--black-color);
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -webkit-appearance: none;
  appearance: none;
  -moz-appearance: textfield;
}
/* Start Header */

.public_header .header_btn {
  padding: 9px 23px;
}
/* ENd Header */
/* Begin Form Style */
.qa-form-label {
  font-size: 24px !important;
  -webkit-transform: none !important;
  transform: none !important;
  position: unset !important;
  white-space: break-spaces !important;
}

.qa-form-input {
  font-size: 28px !important;
  color: #212121 !important;
  margin-top: 0 !important;
  padding-right: 4px;
  padding-left: 4px;
}

/* End Form Style */

/* Start Article  */

.articleSwiper .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
}

.articleSwiper .swiper-slide img {
  display: block;
  width: 100%;
  height: 500px;
  min-height: 300px;
  object-fit: cover;
}

.articleSwiper .swiper-pagination {
  bottom: -0px !important;
}

.recipeSwiper .swiper-slide img {
  display: block;
  width: 100%;
  height: 350px;
  min-height: 200px;
  object-fit: cover;
}
.banner-slider.recent-blog-slider .swiper-button-prev,
.banner-slider.recent-blog-slider .swiper-button-next {
  background-color: var(--white-color);
  width: 35px;
  height: 35px;
  border-radius: 50px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
/* End Article  */

/* Begin Swiper Carousel Style */
.swiper-3d .swiper-slide-shadow {
  background: rgba(255, 255, 255, 0.1);
}

/* End Swiper Carousel Style */

/* Start Banner Section */
.center-left {
  top: 50%;
  left: 8%;
  -webkit-transform: translateX(-8%) translateY(-50%);
  transform: translateX(-8%) translateY(-50%);
}

.banner-slider img {
  width: 100%;
  object-fit: cover;
}

.banner-slider .swiper-button-next:after,
.banner-slider .swiper-button-prev:after {
  font-size: 14px;
  font-weight: bold;
  color: var(--black-color);
}

.banner-slider .swiper-button-prev,
.banner-slider .swiper-button-next {
  background-color: var(--white-color);
  width: 50px;
  height: 50px;
  border-radius: 50px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.banner-slider .swiper-button-prev {
  left: 6%;
}

.banner-slider .swiper-button-next {
  right: 6%;
}

/* End Banner Section */

/* Start Service Section */
.service-section {
  background-size: cover !important;
  padding: 8rem 0 8rem 0;
}

.social_icon {
  width: 40px;
  height: 40px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 100%;
  padding: 7px;
}

.consultant_list_wrap .swiper-slide:nth-child(odd) {
  bottom: -20px;
}
.main_wrapper_box {
  height: 600px;
}
.process_swiper_slide .phone-frame {
  display: none;
}
.process_swiper_slide .swiper-slide-active .phone-frame {
  display: block;
  top: 0;
  left: 0;
}

